    width: 45% !important;
  }
  ._pi_free_text_question_field_container {
    width: 25%;
  }
  div[data-question-type="multiple_choices_question"] ul._pi_answers_container {
    width: 30% !important;
  }
}

@media (min-width: 1301px) and (max-width: 1600px) {
  ._pi_question {
    width: 55% !important;
  }
  ._pi_free_text_question_field_container {
    width: 35%;
  }
  div[data-question-type="multiple_choices_question"] ul._pi_answers_container {
    width: 35% !important;
  }
}

@media (min-width: 901px) and (max-width: 1300px) {
  ._pi_question {
    width: 70% !important;
  }
  ._pi_free_text_question_field_container {
    width: 45%;
  }
  div[data-question-type="multiple_choices_question"] ul._pi_answers_container {
    width: 55% !important;
  }
}

@media (min-width: 601px) and (max-width: 900px) {
  ._pi_question {
    width: 95% !important;
  }
  ._pi_free_text_question_field_container {
    width: 75%;
  }
  div[data-question-type="multiple_choices_question"] ul._pi_answers_container {
    width: 80% !important;
  }
  ._pi_question_custom_content_question p:first-child,
  div._pi_question,
  div._pi_thankYouSurvey {
    font-size: 22px !important;
  }
  ._pi_question_custom_content_question button.pi_question_link,
  ._pi_question_custom_content_question p,
  input._pi_free_text_question_field,
  input._pi_free_text_question_submit_button,
  input._pi_multiple_choices_question_submit_button,
  label._pi-control._pi-control-checkbox,
  textarea._pi_free_text_question_field,
  ul._pi_answers_container li a label {
    font-size: 16px !important;
  }
  ._pi_closeButton {
    font-size: 18px !important;
  }
  a._pi_branding,
  a._pi_branding:hover,
  div._pi_free_text_question_characters_count,
  div._pi_multiple_choices_count {
    font-size: 12px !important;
  }
}

@media (max-width: 600px) {
  ._pi_question {
    padding: 15px 0;
  }
  ._pi_question_custom_content_question p:first-child,
  div._pi_question,
  div._pi_thankYouSurvey {
    font-size: 19px !important;
  }
  ._pi_question_custom_content_question button.pi_question_link,
  ._pi_question_custom_content_question p,
  input._pi_free_text_question_field,
  input._pi_free_text_question_submit_button,
  input._pi_multiple_choices_question_submit_button,
  label._pi-control._pi-control-checkbox,
  textarea._pi_free_text_question_field,
  ul._pi_answers_container li a label {
    font-size: 15px !important;
  }
  ._pi_closeButton {
    font-size: 18px !important;
  }
  a._pi_branding,
  a._pi_branding:hover,
  div._pi_free_text_question_characters_count,
  div._pi_multiple_choices_count {
    font-size: 11px !important;
  }
  #_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="multiple_choices_question"] ._pi_widgetContentContainer ul._pi_answers_container,
  #_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="single_choice_question"] ._pi_widgetContentContainer ul._pi_answers_container,
  div[data-question-type="multiple_choices_question"] ul._pi_answers_container,
  div[data-question-type="single_choice_question"] ul._pi_answers_container {
    display: block !important;
  }
  #_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="multiple_choices_question"] ._pi_widgetContentContainer,
  div[data-question-type="multiple_choices_question"] ._pi_widgetContentContainer {
    width: 80% !important;
  }
  #_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="multiple_choices_question"] ._pi_widgetContentContainer ul._pi_answers_container li,
  div[data-question-type="multiple_choices_question"] ul._pi_answers_container li {
    margin: 0 2% 6px 2% !important;
    min-width: 96% !important;
    max-width: 96% !important;
  }
  #_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="single_choice_question"] ._pi_widgetContentContainer ul._pi_answers_container li,
  div[data-question-type="single_choice_question"] ul._pi_answers_container li {
    margin: 0 2% 6px 2% !important;
    min-width: 96% !important;
    max-width: 96% !important;
  }
  #_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="single_choice_question"] ._pi_widgetContentContainer ul._pi_answers_container li a label,
  div[data-question-type="single_choice_question"] ul._pi_answers_container li a label {
    width: 100% !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
  ._pi_free_text_question_field_container {
    width: 80%;
  }
}

@media (max-width: 414px) {
  #_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="multiple_choices_question"] ._pi_widgetContentContainer,
  div[data-question-type="multiple_choices_question"] ._pi_widgetContentContainer {
    width: 90% !important;
  }
  ._pi_free_text_question_field_container {
    width: 100%;
  }
  #_pi_surveyWidgetContainer.mobile-enabled {
    top: auto;
    bottom: auto;
    position: static;
    margin: auto;
    padding: auto;
    display: auto;
    box-sizing: auto;
    height: auto;
    width: auto;
    z-index: auto;
  }
}

#_pi_surveyWidget,
#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget {
  margin: 0 !important;
  padding: 0;
  border: 0 !important;
  border-top: 2px solid #a2a1a1 !important;
  position: fixed !important;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 -1px 10px rgba(0, 0, 0, 0.2);
}